<template>
  <el-main>
    <h2>批量查询空投结果</h2>
    <div class="param">
      <div class="item">
        <span>合约地址:</span>
        <el-input v-model="contract" placeholder="合约地址，区分大小写"></el-input>
      </div>
      <div class="item">
        <span>账户列表:</span>
        <el-input type="textarea" :rows="10" v-model="wallet" placeholder="一行一个地址"></el-input>
      </div>
    </div>
    <div class="desc">示例：zksync官方随机发的1w个NFT奖励，合约地址为0xD07180c423F9B8CF84012aA28cC174F3c433EE29，"无"表示从未有过，0表示曾经有现为0(如全卖出剩余0)，其余数字表示现有个数。</div>
    <div style="text-align: center">
      <el-button type="primary" @click="getToken">查询</el-button>
    </div>
    <div v-html="result" class="result"></div>
  </el-main>
</template>

<script>
import {getBalance} from "../../../utils/getbalance";

export default {
  name: "Balance",
  data () {
    return {
      contract: "0xD07180c423F9B8CF84012aA28cC174F3c433EE29",
      wallet: "",
      result: ""
    }
  },
  methods: {
    // 查询token
    async getToken() {
      this.result = "";
      let arr = this.wallet.split("\n");
      let patter = /^0x[0-9a-fA-F]{40}$/;
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].match(patter) != undefined) {
          let tokenlist = await getBalance(arr[i]);
          this.result += arr[i] + `:\t` + this.filter(tokenlist) + `\n`;
        }
      }
    },
    // 过滤合约
    filter(tokenlist) {
      console.log(tokenlist[this.contract]);
      if (tokenlist[this.contract] != undefined) {
        let decimals = 1;
        if (tokenlist[this.contract]["token"] != null) {
          decimals = tokenlist[this.contract]["token"]["decimals"];
        }
        return tokenlist[this.contract]["balance"]/decimals;
      } else {
        return "无";
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.el-main
  width 800px
  margin 0 auto
  h2
    text-align center

.param
  margin-bottom 5px
  .item
    margin-top 10px
    span
      font-size 14px
      margin-bottom 10px

.desc
  margin 10px 0 20px 0
  font-size 14px
  color #3a8ee6

.result
  margin-top 20px
  padding 15px
  white-space pre-wrap
  font-size 14px
  color gray
  border #3a8ee6 solid 2px
  border-radius 6px
  line-height 1.5
</style>



